<template>
    <section class="menu">
        <div class="title"></div>
        <table>
            <tbody>
            <tr>
                <th></th>
                <th></th>
            </tr>
            <tr v-for="link in links">
                <td>
                    <i class="fa fa-plus-circle"
                       @click="addLink($index)"
                       v-if="links.length<4"></i>
                    <i class="fa fa-minus-circle"
                       @click="removeLink($index)"
                       v-if="links.length>1"></i>
                    <input type="text" v-model="link.name">
                </td>
                <td>
                    <input type="text" v-model="link.href">
                </td>
            </tr>
            </tbody>
        </table>
        <div class="panel">
            <button @click="saveLinks"></button>
        </div>
    </section>
</template>

<script>
    import {userName}   from '../vuex/getters'
    import {pop}        from '../vuex/actions'
    export default{
        data(){
            return {
                links: null
            }
        },
        created(){
            this.$http.post('/getLinks')
                    .then((response)=> {
                        this.links = JSON.parse(response.body)
                    }, (response)=> {
                        console.log(response)
                    })
        },
        methods: {
            addLink(i){
                this.links.splice(i + 1, 0, {
                    name: '',
                    href: ''
                })
            },
            removeLink(i){
                this.links.splice(i, 1)
            },
            saveLinks(){
                if (this.userName === '') {
                    this.pop({
                        pop: true,
                        content: '',
                        cb1: function () {
                            this.pop({})
                        }.bind(this)
                    })
                    return
                }
                this.$http.post('/setLinks', this.$data)
                        .then(()=> {
                            this.pop({
                                pop: true,
                                content: '',
                                cb1: function () {
                                    this.pop({})
                                }.bind(this)
                            })
                        }, (response)=> {
                            console.log(response)
                        })
            },
        },
        vuex: {
            getters: {
                userName,
            },
            actions: {
                pop,
            }
        }
    }
</script>

<style lang="sass">
    @import "../SCSS/Menu.scss";
</style>


