<template>
    <section class="articleList">
        <button @click="$router.go('/console/editor')">
            
        </button>
        <table>
            <tbody>
                <tr>
                    <th @click="sort('title')"></th>
                    <th @click="sort('date')"></th>
                    <th></th>
                </tr>
                <tr v-for="article in articles">
                    <td @click="detail(article._id)">
                        {{article.title}}
                    </td>
                    <td>
                        {{article.date | dateParse}}
                    </td>
                    <td>
                        <i class="fa fa-pencil-square-o"
                           @click="edit(article._id)">
                        </i>
                        <i class="fa fa-trash"
                           @click="deleteItem(article._id,$index)">
                        </i>
                    </td>
                </tr>
            </tbody>
        </table>
    </section>
</template>
<script>
    import {userName}   from '../vuex/getters'
    import {pop}        from '../vuex/actions'
    export default{
        data(){
            return {
                articles: null,
                sortState:{
                    date:0,
                }

            }
        },
        filters: {
            dateParse: value=>{
                let d = new Date(value)
                return d.getFullYear() + '' +
                        (d.getMonth()+1) + '' +
                        d.getDate() + ''
            }
        },
        created(){
            this.$http.get('/articleList')
                    .then((response)=> {
                        let arcs = JSON.parse(response.body)
                        this.articles = arcs.sort((i, j)=>{
                            return new Date(j.date) - new Date(i.date)
                        })
                    }, (response)=> {
                        console.log(response)
                    })
        },
        methods:{
            edit(id){
                this.$router.go('/console/editor?id='+id)
            },
            deleteItem(id,index){
                if(this.userName===''){
                    this.pop({
                        pop:true,
                        content:'',
                        cb1:function() {
                            this.pop({})
                        }.bind(this)
                    })
                    return
                }
                this.pop({
                    pop:true,
                    content:'',
                    cb1:function () {
                        this.$http.post('/delete',{id})
                                .then(()=> {
                                    this.pop({
                                        pop:true,
                                        content:'',
                                        cb1:function () {
                                            this.pop({})
                                            this.articles.splice(index,1)
                                        }.bind(this)
                                    })
                                }, (response)=> {
                                    console.log(response)
                                })
                    }.bind(this),
                    cb2:function () {
                        this.pop({})
                    }.bind(this)
                })

            },
            detail(id){
                this.$router.go('/article?id='+id)
            },
            sort(opt){
                if(opt=='date'){
                    if(this.sortState.date===0){
                        this.articles = this.articles.sort((i, j)=>{
                            return new Date(i.date) - new Date(j.date)
                        })
                        this.sortState.date=1
                    }else {
                        this.articles.reverse()
                    }
                }else if(opt=='title'){
                    this.articles.reverse()
                }
            }
        },
        vuex: {
            getters: {
                userName,
            },
            actions:{
                pop,
            }
        }
    }
</script>
<style lang="sass">
    @import "../SCSS/ArticleList.scss";
</style>


